import teachersBase from "./TeachersBase";

const TeachersCard = () => {
  return (
    <>
      {teachersBase.map((item) => (
        <div
          className="col-lg-4 col-md-6 col-12 mb-md-5 mb-4 teachers-card"
          data-aos={item.aos}
          data-aos-duration="800"
          data-aos-delay={item.aosDelay}
          data-aos-once="true"
          key={item.id}
        >
          <div className="img-box">
            <img src={item.imgUrl} alt={item.teacher} />
          </div>
          <div className="text-box">
            <p className="date">{item.course}</p>
            <h3>{item.teacher}</h3>
          </div>
        </div>
      ))}
    </>
  );
};

export default TeachersCard;
