import "./Teachers.css";
import TeachersCard from "./TeachersCard";

const Teachers = ({ activeTeachers }) => {
  return (
    <section
      className={activeTeachers ? "teachers teachers-click-style" : "teachers"}
    >
      <h2 data-aos="fade-right" data-aos-duration="600" data-aos-once="true">
        Ustozlar
      </h2>
      <div className="row align-items-start">
        <TeachersCard />
      </div>
    </section>
  );
};

export default Teachers;
